#panels-ipe-tray {
  display: none;
}
#front-layout-wrapper {
  .section {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;

    .scroll-down {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 10px 0;

      i {
        position: relative;
        transition: $transNorm;
      }
      &:hover {
        cursor: pointer;
        i {
          animation: bounce 2s infinite 1s;
        }
      }
    }
  }

  /*--------------------------------------
                SECTION 1
  ---------------------------------------*/
  .first-section {
    @media screen and (min-width: $desktop) {
      height: calc(100vh - 50px);
    }
    .logo {
      width: 80%;
      margin: 30px;
      transition: $transNorm;

      @media screen and (orientation: landscape) {
        width: 40%;
      }

      @supports (animation:auto) {
        animation: blur-in 3s ease-in-out;
        animation-fill-mode: forwards;
      }

      path, rect, circle {
        fill: $black;
      }
      .cls-1 {
        transition: fill 10s ease-out;
        fill: $blue;
      }

      &:hover {
        .cls-1 {
          animation: logo-flicker 5s ease-in-out 3s;
          animation-fill-mode: forwards;
        }
      }


    }

    .button {
      @supports (animation:auto) {
        opacity: 0;
        transform: translateY(-15px);
        animation: slide-down 1s ease-in-out 3s;
        animation-fill-mode: forwards;
      }
    }
    .scroll-down {
      @supports (animation:auto) {
        opacity: 0;
        transform: translateY(-15px);
        animation: slide-down 1s ease-in-out 5s;
        animation-fill-mode: forwards;
      }
    }
  }

  /*--------------------------------------
                SECTION 2
  ---------------------------------------*/
  .second-section {
    height: 100vh;
    background-color: $blue;
    color: $white;
    padding: $padding / 2;

    @media screen and (min-width: $desktop) {
      padding: $padding ($padding * 2);
    }

    blockquote {
      @media screen and (min-width: $desktop) {
        font-size: $fontLarge;
      }
      p {
        text-align: center;
        hyphens: none;
      }
    }
  }

  /*--------------------------------------
                SECTION 3
  ---------------------------------------*/

  .third-section {
    height: auto;
    min-height: 100vh;
    padding: $padding / 2;

    @media screen and (min-width: $desktop) {
      padding: 0 ($padding * 3);
    }

    .block-region-third-section {
      width: 100%;
      height: 100%;
    }

    .rollover-image {
      height: auto;
      @media screen and (orientation:landscape) {
        height: 50vh;
        overflow: hidden;
      }

      img {
        @supports (transform:translate(-50%, -50%)) {
          position: absolute;
          top: 50%;
          left: 50%;
          transform:translate(-50%, -50%)
        }
      }
    }
  }

  /*--------------------------------------
                SECTION 4
  ---------------------------------------*/

  .fourth-section {
    height: auto;
    min-height: 100vh;
    padding: $padding / 2;

    @media screen and (min-width: $desktop) {
      padding: 0 ($padding * 3);

      span {
        font-size: $fontLarge;
      }
    }

    span {
      text-align: center;
    }

    div {
      display: flex;
      flex-direction: column;

    }
  }
}
