/*--------------------------------------
              BREADCRUMBS
---------------------------------------*/

#block-breadcrumbs {
  text-transform: lowercase;
  font-size: $fontSmall - 0.2em;
  margin-bottom: 30px;
  @media screen and (min-width: $desktop) {
    font-size: $fontSmall;
  }
  ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;

    li {
      padding: 0 3px;
    }
  }
}
