/*--------------------------------------
          BASIC e.g. Front Page
---------------------------------------*/
.image-view {
  .rollover-image {
    border: 1px solid $black;
    display: block;
    position: relative;
    color: $black;
    text-transform: uppercase;
    transition: $transSlow;
    height: auto;
    min-height: 100px;
    overflow: hidden;

    img {
      width: 100%;
      height: auto;
    }

    .overlay {
      background-color: $whiteoverlay;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: $transSlow;
    }

    &:hover {
      @supports (filter:grayscale(100%)) {
        filter: grayscale(100%);
      }
      box-shadow: $shadowBold;
      .overlay {
        opacity: 1;
      }
    }
  }
}

/*--------------------------------------
            PORTFOLIO PAGE
---------------------------------------*/

.gallery-view {
  border: 1px solid $black;
  display: block;
  position: relative;
  color: $black;
  text-transform: uppercase;
  transition: $transSlow;
  height: auto;
  min-height: 100px;
  overflow: hidden;
  @supports (filter:grayscale(100%)) {
    filter: grayscale(100%);
  }

  img {
    width: 100%;
    height: auto;
  }

  .overlay {
    background-color: $whiteoverlay;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: $transSlow;
    padding: 30px;

    span {
      text-align: center;
      overflow-wrap: break-word;
      word-wrap: break-word;
      -webkit-hyphens: auto;
      -ms-hyphens: auto;
      -moz-hyphens: auto;
      hyphens: auto;
    }
  }

  &:hover {
    @supports (filter:grayscale(100%)) {
      filter: grayscale(0%);
    }
    box-shadow: $shadowBold;
    .overlay {
      opacity: 0;
    }
  }
}

/*--------------------------------------
            TAXONOMY PAGES
---------------------------------------*/
.taxonomy-view {
  border: 1px solid $black;
  display: block;
  position: relative;
  color: $black;
  text-transform: uppercase;
  transition: $transSlow;
  height: auto;
  min-height: 100px;
  overflow: hidden;
  @supports (filter:grayscale(100%)) {
    filter: grayscale(100%);
  }

  img {
    width: 100%;
    height: auto;
  }


  &:hover {
    @supports (filter:grayscale(100%)) {
      filter: grayscale(0%);
    }
    box-shadow: $shadowBold;
  }
}
