@supports (filter:blur) {
  @keyframes blur-in {
    from {
      opacity: 0;
      filter: blur(10px);
    }
    to {
      opacity: 1;
      filter: blur(0px);
    }
  }
}


@supports (transform:translateY) {
  @keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-15px);
    }
    to {
      opacity: 1;
      transform: translateY(0px);
    }
  }
}

@keyframes bounce {
  0% {
    top: 0;
  }
  25% {
    top: -10px;
  }
  50% {
    top: 10px;
  }
  100% {
    top: 0;
  }
}

.logo-flicker {
  animation: logo-flicker 5s ease-in-out 3s;
  animation-fill-mode: forwards;
}

@keyframes logo-flicker {
  0% {
    fill: $black;
  }
  5% {
    fill: $blue;
  }
  10% {
    fill: $blue;
  }
  20% {
    fill: $blue;
  }
  25% {
    fill: $black;
  }
  30% {
    fill: $blue;
  }
  100% {
    fill: $black;
  }
}

/*--------------------------------------
          SCROLLNIMATION
---------------------------------------*/

.scrollnimate {


  &.sn-fade-in {
    opacity: 0;
    @supports (filter: blur()) {
      filter: blur(10px);
    }
    transition: all 1s ease-in-out 0.5s;


    &.scrollnimate-fade-in {
      opacity: 1;
      @supports (filter: blur()) {
        filter: blur(0px);
      }
    }
  }
}
