/*--------------------------------------
              MAIN MENU
---------------------------------------*/
#main-menu {
  position: fixed;
  height: $navHeight;
  width: $navHeight;
  right: 0;
  background-color: $white;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 100;
  transition: $transNorm;
  letter-spacing: $spacing;
  @media screen and (orientation:landscape) {
    flex-direction: row;
    justify-content: center;
  }
  @media screen and (min-width: $desktop) {
    justify-content: space-between;
  }
  &.opened {
    height: auto;
  }
  &.scroll {
    box-shadow: $shadow;
  }

  #branding {
    display: flex;
    flex-direction: column;
    align-items: center;
    order: 1;
    opacity: 0;
    @media screen and (orientation: landscape) {
      width: 50%;
    }
    @media screen and (min-width: $desktop) {
      order: 0;
      flex-direction: row;
      height: 100%;
      padding: 15px 0;
      margin: 0 10px;
      width: auto;
      z-index: 10;
    }

    #block-sitebranding {
      width: 30%;
      @media screen and (min-width: $iphoneSE) {
        width: 50%;
      }
      @media screen and (min-width: $desktop) {
        height: 100%;
        width: auto;
      }

      .logo {
        width: 100%;
        height: auto;
        display: block;
        margin-bottom: $navHeight;

        @media screen and (orientation:landscape) {
          width: 50%;
          margin: 0 auto $navHeight auto;
        }
        @media screen and (min-width: $desktop) {
          width: auto;
          height: 100%;
        }
        svg {
          width: 100%;
          height: auto;
          fill: $black;
          transition: $transNorm;
          @media screen and (min-width: $desktop) {
            height: 100%;
            width: auto;
          }     
        }

        &:hover {
          svg {
            fill: $blue;
          }
        }
        &.is-active {
          svg {
            fill: $blue;
          }
        }
      }
    }
    #block-socializerprofilelinks {
      width: 100%;
      .socializer {
      }
      @media screen and (min-width: $desktop) {
        width: auto;
        font-size: 0.75em;
        margin-left: 20px;
        a {
          opacity: 0.5;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  #nav-menu {
    display: none;
    opacity: 0;
    order: 0;
    @media screen and (orientation:landscape) {
      width: 50%;
    }
    @media screen and (min-width: $desktop) {
      order: 1;
      width: auto;
      position: absolute;
      margin: 0 auto;
      display: flex;
      width: 100%;
      justify-content: center;
      z-index: 0;
      //font-size: $fontSmall;
    }

    ul {
      padding: $navHeight 0;
      margin: 0;
      list-style: none;
      text-transform: lowercase;
      @media screen and (orientation:landscape) {
        padding: 0;
      }
      @media screen and (min-width: $desktop) {
        display: flex;
      }

      li {
        a {
          display: block;
          text-align: center;
          color: $black;
          padding: 10px;
          @media screen and (min-width: $desktop) {
            padding: 10px 25px;
          }
          &:hover {
            color: $blue;
          }

          &.is-active {
            color: $blue;
          }
        }
      }
    }
  }
  .menu-button {
    position: absolute;
    height: $navHeight;
    width: $navHeight;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: $transNorm;
    @media screen and (min-width: $desktop) {
      order: 2;
      z-index: 10;
    }
    i {
      font-size: $buttonSize;
    }
    &.opened {
      color: $blue;
    }
  }
}


/*--------------------------------------
              SHARE MENU
---------------------------------------*/
.share-menu {
  display: flex;
  flex-wrap: wrap;

  .share-menu-button {
    font-size: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75px;
    height: 75px;
    transition: $transNorm;

    &:hover {
      cursor: pointer;
      color: $blue;
    }

    &.active {
      color: $blue;
      text-shadow: $shadow;
      transform: rotate(90deg);
    }

  }

  .share-menu-expand {
    display: none;
    width: 100%;
    padding: 0 10px;

    .socializer {
      justify-content: flex-start;
    }
  }
}
