#panels-two-column-reverse{
  display: flex;
  flex-direction: column;
  @media screen and (min-width: $desktop) {
    flex-direction: row;
  }

  .column-a {
    order: 1;
    margin: ($padding / 3) 0;
    @media screen and (min-width: $desktop) {
      width: 50%;
      order: 0;
      margin: 0;
      padding: 0 ($padding);
    }
  }
  .column-b {
    order: 0;
    margin: ($padding / 3) 0;
    @media screen and (min-width: $desktop) {
      width: 50%;
      order: 1;
      margin: 0;
      padding: 0 ($padding);
    }
  }

  .offset {
    @media screen and (min-width: $desktop) {
      margin-top: 20% !important;
    }
  }
}
