#layout-wrapper, #front-layout-wrapper {
  position: relative;
  width: 100%;
  z-index: 10;

  main {
    background-color: $white;
    margin-bottom: calc(50vh - 5px);
    box-shadow: $shadow;
    padding-bottom: $padding * 2;
    @media screen and (min-width: $desktop) {
      padding-top: $navHeight;
    }
  }
}


#block-pagetitle {
  h1 {
    margin: 30px 0 5px 0;
  }
}

#layout-wrapper {
  main {
    padding: ($padding / 2) ($padding / 3);
    min-height: 100vh;

    @media screen and (min-width: $desktop) {
      padding: ($padding / 2) $padding $padding $padding;
    }
  }
}
