#panels-gallery {
  .content {
    .block-region-content {
      min-height: 100vh;
      .term-view {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @media screen and (min-width: $iphone6) and (orientation: landscape) {
          padding: 0 (($padding / 2) - 20px);
        }
        @media screen and (min-width: $desktop) {
          //padding: 0 20px;
          justify-content: center;
        }
      }


      .views-element-container {
        width: 100%;

        .views-row {
          padding: 20px 0;

          @media screen and (min-width: $iphone6) and (orientation: landscape) {
            width: 50%;
            padding: 20px;
          }

          @media screen and (min-width: $desktop) {
            width: 33.333333%;
          }
        }

      }
    }
  }

  /*--------------------------------------
                MINI PAGER
  ---------------------------------------*/

  nav {
    width: 100%;

    @media screen and (min-width: $desktop) {
      width: 100%;
      top: 50%;
      position: absolute;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: $fontLarge;
      width: 100%;
      display: flex;
      justify-content: center;
      @media screen and (min-width: $desktop) {
        display: block;
        width: auto;
      }

      li {
        padding: 20px;
        text-shadow: $shadowBold;
        transition: $transNorm;

        @media screen and (min-width: $desktop) {
          display: block;
          position: absolute;

          &.previous {
            left: - ($padding / 2);
            transform: translateY(-50%);
          }
          &.next {
            right: - ($padding / 2);
            transform: translateY(-50%);
          }
        }

        &.disabled {
          text-shadow: none;
          opacity: 0.1;
        }
        a {
          color: $black;

          &:hover {
            color: $blue;
          }
        }

      }
    }
  }
}
