/*--------------------------------------
          UNIVERSAL TYPOGRAPHY
---------------------------------------*/
body {
  font-family: $primaryfont;
  font-size: 1.6em;
  color: $black;
  font-weight: 300;

  @media screen and (min-width: $desktop) {
    font-size: 1.3em;
  }
}

a {
  text-decoration: none;
  transition: $transNorm;
  color: $blue;
}

blockquote {
  z-index: 1;
  &:before {
    content: open-quote;
    position: absolute;
    font-size: 4em;
    top: -0.35em;
    left: -0.1em;
    opacity: 0.3;
    font-family: 'Times', serif;
    z-index: 0;
  }
}

h1 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: $spacingLarge;
  font-size: 1.2em;
  @media screen and (min-width: $desktop) {
    font-size: 1.8em
  }
}

h2 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: $spacingLarge;
  font-size: 1.1em;
  @media screen and (min-width: $desktop) {
    font-size: 1.5em
  }
}

.date {
  text-transform: uppercase;
  letter-spacing: $spacing;
  font-weight: 700;
  font-size: $fontSmall;
  display: block;
}

ul {
  list-style: square;
}

/*--------------------------------------
      DEALING WITH LONG ASS WORDS
---------------------------------------*/
h1, h2, h3, h4, h5 {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
}



/*--------------------------------------
    JUSTIFICATION - 'Cause it's pretty
---------------------------------------*/
@supports (hyphens:auto) {
  @media screen and (min-width: $iphoneSE) {
    p {
      hyphens: auto;
      text-align: justify;
    }
    .text-align-center {
      text-align: center;
    }
  }
}

