#panels-portfolio {
  .content {
    .block-region-content {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      @media screen and (min-width: $iphone6) and (orientation: landscape) {
        padding: 0 (($padding / 2) - 20px);
      }
      @media screen and (min-width: $desktop) {
        justify-content: center;
      }

      .views-element-container {
        width: 100%;
        padding: 20px 0;

        @media screen and (min-width: $iphone6) and (orientation: landscape) {
          width: 50%;
          padding: 20px;
        }

        @media screen and (min-width: $desktop) {
          width: 33.333333%;
        }
      }
    }
  }
}
