#panels-gallery-content {
  @media screen and (min-width: $desktop) {
    display: flex;
    align-items: flex-start;
    padding-top: $padding;
    flex-wrap: wrap;
  }

  .gallery-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media screen and (min-width: $desktop) {
      width: 50%;
      padding-right: $padding / 2 ;
    }

    .selected-image {
      width: auto;
      height: auto;
      position: relative;
      overflow: hidden;
      padding: 0 5px;
      margin: 0 auto;

      img {
        width: auto;
        max-width: 100%;
        height: auto;
        border: 1px solid $black;
      }
      .pin-it {
        position: absolute;
        top: 0;
        right: 10px;
        padding: 10px;
        box-shadow: $shadowBold;
        opacity: 1;
        transform: translateY(-150%);
        z-index: 100;
      }
      .image {
        width: auto;
        height: auto;
        display: inline-block;
      }

      &:hover {
        .pin-it {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }

    .block-region-gallery > div {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding: 5px 0;
      height: auto;
      align-items: center;
      justify-content: space-between;
    }

    .gallery-image {
      width: calc(50% - 10px);
      height: 0%;
      max-height: 20vh;
      margin: 5px;
      overflow: hidden;
      border: 1px solid $black;
      flex-basis: content;
      @media screen and (min-width: $desktop) {
        width: calc(33.333333% - 10px);
      }
      &:hover {
        cursor: pointer;
      }

      &.selected {
        box-shadow: $shadowBold;

        img {
          @supports (filter:grayscale(0%)) {
            filter: grayscale(0%);
          }
        }
      }

      img {
        width: 100%;
        height: auto;
        opacity: 0.3;
        transition: $transNorm;

        @supports (filter:grayscale(100%)) {
          filter: grayscale(100%);
        }
      }
    }
  }

  .content-wrapper {
    @media screen and (min-width: $desktop) {
      width: 50%;
      padding-left: $padding / 2 ;
    }
    .divider {
      @media screen and (max-width: $desktop) {
        @include divider($black, 1px, 50%);
        margin: ($padding / 2) auto;
      }
    }

    .date {
      text-align: center;
    }
  }

  .content-bottom {
    width: 100%;
    padding-top: $padding * 2;

    .block-region-bottom {
      //min-height: 0vh;
      .term-view {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @media screen and (min-width: $iphone6) and (orientation: landscape) {
          padding: 0 (($padding / 2) - 20px);
        }
        @media screen and (min-width: $desktop) {
          //padding: 0 20px;
          justify-content: center;
        }
      }


      .views-element-container {
        width: 100%;

        .views-row {
          padding: 20px 0;

          @media screen and (min-width: $iphone6) and (orientation: landscape) {
            width: 50%;
            padding: 20px;
          }

          @media screen and (min-width: $desktop) {
            width: 33.333333%;
          }
        }

      }
    }

  }
}
