footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 ($padding / 2);
  background-color: $black;
  color: $white;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 0;
  height: 50vh;

  a {
    width: 60%;
    display: block;
    margin: 30px;
    @media screen and (orientation: landscape) {
      width: 30%;
    }
    svg {
      fill: $white;
    }
  }
  span {
    text-align: center;
    hyphens: none;
    font-size: $fontTiny;

    a {
      display: inline;
      margin: 0;
      color: $green;
    }
  }

  div {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
