@mixin button($color) {
  display: block;
  color: $color;
  text-transform: uppercase;
  font-size: $fontSmall;
  border: 1px solid $color;
  padding: 8px 25px;
  margin: 30px 0;
  i {
    display: inline-block;
    margin-left: 10px;
  }

  @media screen and (min-width: $desktop) {
    margin: 30px;
    transition: $transNorm;

    i {
      position: relative;
      opacity: 0;
      right: 0.5em;
      width: 0;
      margin-left: 0;
      transition: $transNorm;
      text-align: right;
    }

    &:hover {
      i {
        margin-left: 10px;
        width: 1em;
        opacity: 1;
        right: 0;
      }
    }
  }
}

.button {
  @include button($black);
}

.pin-it {
  background-color: #cb2027;
  display: block;
  color: $white;
}



.socializer {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  font-size: 0.8em;
  @media screen and (min-width: $iphoneSE) {
    font-size: 1em;
  }
  a {
    display: block;
    padding: 0.5em;
    color: $black;
  }
  &.no-email {
    .e-mail {
      display: none;
    }
  }

  li {
    &.facebook {
      a:hover {
        color: #3b5998;
      }
    }

    &.twitter {
      a:hover {
        color: #55acee;
      }
    }

    &.linkedin {
      a:hover {
        color: #007bb5;
      }
    }

    &.google-plus {
      a:hover {
        color: #dd4b39;
      }
    }

    &.pinterest {
      a:hover {
        color: #cb2027;
      }
    }

    &.behance {
      a:hover {
        color: #1769ff;
      }
    }

    &.dribbble {
      a:hover {
        color: #ea4c89;
      }
    }

    &.youtube {
      a:hover {
        color: #bb0000;
      }
    }

    &.e-mail {
      a:hover {
        color: #2c2c2c;
      }
    }
  }
}
