/*--------------------------------------
              TYPOGRAPHY
---------------------------------------*/
$primaryfont: 'Effra', 'Tahoma', sans-serif;

$fontLarge: 1.5em;
$fontSmall: 0.7em;
$fontTiny: 0.5em;

$spacing: 0.05em;
$spacingLarge: 0.075em;

$buttonSize: 1em;

$padding: 100px;


/*--------------------------------------
                COLORS
---------------------------------------*/
$white: #efeeeb;
$black: #635e59;
$blue: #009ace;
$green: #a5dd10;

$whiteoverlay: rgba(239, 238, 235, 0.9);

$shadow: 0 3px 5px rgba(99, 94, 89, 0.2);
$shadowBold: 0 3px 5px rgba(99, 94, 89, 0.5);
$shadowInset: 0 2px 3px rgba(99, 94, 89, 0.3) inset;


/*--------------------------------------
                RANDOM
---------------------------------------*/
$navHeight: 50px;

/*--------------------------------------
              ANIMATION
---------------------------------------*/
$transNorm: all 0.4s ease-in-out 0.2s;
$transSlow: all 1s ease-in-out 0.2s;


/*--------------------------------------
              RESPONSIVE
---------------------------------------*/

$desktop: 1081px;
$iphone7: 737px;
$iphone6: 481px;
$iphoneSE: 321px;
