form {
  width: 100%;

  .form-item {
    margin: 20px 0;
    label {
      width: 100%;
      display: block;
      text-transform: lowercase;
      margin-bottom: 5px;
    }
    input, textarea {
      width: 100%;
      font-size: 1em;
      font-weight: 300;
      padding: 10px 20px;
      border: none;
      box-shadow: $shadowInset;
      color: $black;
    }
  }
  input#edit-submit {
    background-color: transparent;
    cursor: pointer;
    margin: 30px auto;
    padding: 8px 35px;

    &:hover {
      border-color: $blue;
      color: $blue;
    }
  }
}
