#cookie-popup {
  width: 100%;
  position: fixed;
  z-index: 900;
  bottom: -100%;
  left: 0;
  background-color: $black;
  color: $white;
  display: flex;
  font-size: $fontSmall;
  padding: 20px;
  flex-direction: column;
  align-items: center;

  @media screen and (orientation: landscape) {
    flex-direction: row;
    justify-content: center;
  }

  span {
    text-align: center;
    @media screen and (orientation: landscape) {
      text-align: left;
    }
  }

  a {
    color: $green;
  }

  .button {
    color: $white;
    border: none;
    background-color: $blue;
    margin: 20px 0 0 0;
    width: 50%;
    text-align: center;
    height: 0%;
    cursor: pointer;
    font-weight: 400;

    @media screen and (orientation: landscape) {
      margin: 0 0 0 20px;
    }

    @media screen and (min-width: $desktop) {
      width: 15%;
    }

    &:hover {
      background-color: $green;
      color: $black;
    }
  }
}
